import Vue from 'vue'
import App from './App.vue'
import BootstrapVue from 'bootstrap-vue'
import Octicon from 'vue-octicon/components/Octicon.vue'
import VueCodemirror from 'vue-codemirror'
import Notifications from 'vue-notification'
import Vuex from 'vuex'
import VueRouter from "vue-router";

import 'vue-octicon/icons/gear'
import 'vue-octicon/icons/grabber'
import './style.scss'
import 'codemirror/lib/codemirror.css'

Vue.use(VueRouter);
Vue.use(Vuex)
Vue.use(BootstrapVue)
Vue.use(Notifications)
Vue.use(VueCodemirror)
Vue.component('octicon', Octicon);

const store = new Vuex.Store({
  state: {
    showLoading: false,
    showLeft: true,
    showLog: false,
    currentDir: null,
    currentFile: {
      filePath: null,
      content: null
    }
  },
  mutations: {
    setShowLoading(state, show) {
      state.showLoading = show;
    },
    setShowLeft(state, show) {
      state.showLeft = show;
    },
    setShowLog(state, show) {
      state.showLog = show;
    },
    setCurrentDir(state, dir) {
      state.currentDir = dir;
    },
    setCurrentFile(state, file) {
      state.currentFile = file;
    }
  },
  getters: {
    getShowLoading: state => {
      return state.showLoading;
    },
    getShowLeft: state => {
      return state.showLeft;
    },
    getShowLog: state => {
      return state.showLog;
    },
    getCurrentDir: state => {
      return state.currentDir;
    },
    getCurrentFile: state => {
      return state.currentFile;
    }
  }
});

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App),
}).$mount('#app')