export function getFilename(filePath) {
	var elements = filePath.split('/');
	return elements[elements.length - 1];
}
export function getCurrentPath() {
	var search = window.location.search;
	if (search.length > 2) {
		search = search.substring(1, search.length);
		var keyValueStrList = search.split('&');
		for (var i = 0; i < keyValueStrList.length; i++) {
			var keyValue = keyValueStrList[i].split('=');
			if (keyValue[0] == 'path') {
				return decodeURIComponent(keyValue[1]);
			}
		}
	}
	return '/';
}
export function isFolder(iconName) {
	return iconName.indexOf('folder') === 0;
}
export function isImage(iconName) {
	return iconName.indexOf('bmp') === 0 ||
		iconName.indexOf('png') === 0 ||
		iconName.indexOf('jpg') === 0 ||
		iconName.indexOf('jpeg') === 0;
}
export function isScript(filePath) {
	const p = filePath.toLowerCase();
	return p.endsWith('.lua') ||
		p.endsWith('.at') ||
		p.endsWith('.ate');
}

export function isLuaPackage(filePath) {
	const p = filePath.toLowerCase();
	return p.endsWith('.at');
}

export function isLuaFile(filePath) {
	const p = filePath.toLowerCase();
	return p.endsWith('.lua');
}

export function getFileType(filePath) {
	const p = filePath.toLowerCase();
	if (p.endsWith('.lua')) return 'lua';
	if (p.endsWith('.txt')) return 'txt';
	if (p.endsWith('.bmp')
		|| p.endsWith('.png')
		|| p.endsWith('.jpg')
		|| p.endsWith('.jpeg'))
		return 'img';
	return 'unkown';
}