<template>
  <div>
    <div class="toolbar">
      <div v-if="currentScript.filePath" v-text="'File: ' + currentScript.filePath"></div>
      <div v-else></div>
      <div class="d-flex flex-row align-items-center">
        <template v-if="code">
          <div>(Ctrl+r,⌘+r)</div>
          <button class="btn btn-secondary btn-sm m-2" v-on:click="play()">Play</button>
          <div>(Ctrl+s,⌘+s)</div>
          <button
            class="btn btn-secondary btn-sm ml-2"
            v-on:click="saveFile()"
            v-if="!isSaving"
          >Save</button>
          <button class="btn btn-secondary btn-sm ml-2" disabled v-else>Saving</button>
        </template>
      </div>
    </div>
    <codemirror ref="editor" v-model="code" :options="cmOptions"></codemirror>
  </div>
</template>
<script>
import { codemirror } from "vue-codemirror";
import "codemirror/mode/lua/lua.js";
import "codemirror/theme/base16-dark.css";
import { mapGetters } from "vuex";
const axios = require("axios");

if (process.env.NODE_ENV === "development") {
  const host = require("../config.json").host;
  axios.defaults.baseURL = host;
}

export default {
  name: "Editor",
  components: {
    codemirror
  },
  data() {
    return {
      isSaving: false,
      code: this.currentScript,
      cmOptions: {
        // codemirror options
        mode: "text/x-lua",
        indentUnit: 4,
        tabSize: 4,
        indentWithTabs: true,
        smartIndent: true,
        lineNumbers: true,
        line: true
        // more codemirror options, 更多 codemirror 的高级配置...
      }
    };
  },
  methods: {
    onCmReady(cm) {
      console.log("the editor is readied!", cm);
    },
    onCmFocus(cm) {
      console.log("the editor is focus!", cm);
    },
    onCmCodeChange(newCode) {
      console.log("this is new code", newCode);
      this.code = newCode;
    },
    saveFile() {
      const self = this;

      self.isSaving = true;

      const filePath = this.currentScript.filePath;
      var content = this.codemirror.getValue();

      var url = "/file/update?path=" + encodeURIComponent(filePath);
      var params = "content=" + encodeURIComponent(content);

      axios
        .post(url, params)
        .then(function(response) {
          var respObj = response.data;
          if (respObj.status == "success") {
            self.$store.commit("setCurrentFile", {
              filePath,
              content
            });
          } else {
            self.$notify({
              group: "main",
              type: "error",
              text: respObj.info
            });
          }
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .then(function() {
          self.isSaving = false;
        });
    },
    onHotKey(e) {
      if (
        e.keyCode == 83 &&
        (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)
      ) {
        e.preventDefault();
        this.saveFile();
      } else if (
        e.keyCode == 82 &&
        (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)
      ) {
        e.preventDefault();
        this.play();
      }
    },
    play() {
      const self = this;
      const filePath = this.currentScript.filePath;
      var url = "/control/start_playing?path=" + encodeURIComponent(filePath);
      axios
        .get(url)
        .then(function(response) {
          var respObj = response.data;
          if (respObj.status != "success") {
            self.$notify({
              group: "main",
              type: "error",
              text: respObj.info
            });
          }
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        });
    }
  },
  computed: {
    ...mapGetters({
      currentScript: "getCurrentFile"
    }),
    codemirror() {
      return this.$refs.editor.codemirror;
    }
  },
  watch: {
    currentScript: function(val) {
      this.code = val.content;
    }
  },
  created() {
    document.addEventListener("keydown", this.onHotKey, false);
  },
  beforeDestroy: function() {
    window.removeEventListener("keydown", this.onkey);
  }
};
</script>
