<template>
  <div class="file-list-field">
    <div class="toolbar" style="padding-left:16px;padding-right:0;">
      <Breadcrumb/>
      <div>
        <button
          class="btn btn-secondary btn-sm mr-2"
          v-on:click.stop.prevent="loadFiles()"
        >Refresh</button>
        <button
          class="btn btn-secondary btn-sm mr-2"
          v-on:click.stop.prevent="newFolder()"
        >New Folder</button>
        <button class="btn btn-secondary btn-sm mr-2" v-on:click.stop.prevent="newFile()">New Script</button>
        <button class="btn btn-secondary btn-sm mr-2" v-b-modal.modal-upload-file>Upload File</button>
      </div>
    </div>
    <div class="file-list-wrapper">
      <table class="table table-striped" border="0" cellspacing="0" cellpadding="0">
        <thead>
          <tr>
            <th>Filename</th>
            <!-- <th>File Size</th> -->
            <th>Control</th>
            <th>Options</th>
          </tr>
        </thead>
      </table>
      <div class="file-list-content-wrapper">
        <table class="table table-striped" border="0" cellspacing="0" cellpadding="0">
          <tbody>
            <tr v-for="file in files" v-bind:key="fileName(file)">
              <td>
                <img :src="fileIcon(file.iconName)">
                <router-link
                  v-if="isFolder(file.iconName) || isLuaPackage(file.filePath)"
                  :to="file.filePath"
                >{{ fileName(file) }}</router-link>
                <a v-else href="#" v-on:click.stop.prevent="clickOnFile(file)">{{ fileName(file) }}</a>
              </td>
              <!-- <td>{{ file.fileSize }}</td> -->
              <td>
                <template v-if="isScript(file.filePath)">
                  <button
                    class="btn btn-outline-secondary btn-sm"
                    v-if="!isRunning(file.filePath)"
                    v-on:click.stop.prevent="play(file.filePath)"
                  >Play</button>
                  <button
                    class="btn btn-secondary btn-sm"
                    v-else
                    v-on:click.stop.prevent="stop(file.filePath)"
                  >Stop</button>
                </template>
              </td>
              <td>
                <b-dropdown variant="button" size="sm" no-caret>
                  <template slot="button-content">
                    <octicon name="gear"></octicon>
                  </template>
                  <b-dropdown-item v-on:click.stop.prevent="deleteFile(file.filePath)">Delete</b-dropdown-item>
                  <b-dropdown-item v-on:click.stop.prevent="renameFile(file.filePath)">Rename</b-dropdown-item>
                </b-dropdown>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <b-modal id="modal-upload-file" ref="modal-upload-file" title="Upload File" hide-footer>
      <form
        enctype="multipart/form-data"
        method="post"
        id="uploadForm"
        class="d-flex justify-content-between"
      >
        <input id="file" name="file" type="file">
        <input
          type="submit"
          value="Upload"
          id="uploadSubmit"
          v-on:click.stop.prevent="uploadFile()"
        >
      </form>
    </b-modal>
  </div>
</template>

<script>
let common = require("../common");
import Breadcrumb from "./Breadcrumb.vue";
const axios = require("axios");

if (process.env.NODE_ENV === "development") {
  const host = require("../config.json").host;
  axios.defaults.baseURL = host;
}

export default {
  name: "FileList",
  components: {
    Breadcrumb
  },
  data() {
    return {
      files: [],
      currentPath: "/",
      runningScripts: [],
      refreshIntervalId: null
    };
  },
  created() {
    this.currentPath = this.$route.path;
    this.$store.commit("setCurrentDir", this.currentPath);
    this.loadFiles();
    this.refreshIntervalId = setInterval(() => {
      this.getRunningScripts();
    }, 2000);
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.currentPath = this.$route.path;
    this.$store.commit("setCurrentDir", this.currentPath);
    this.loadFiles();
  },
  destroyed() {
    clearInterval(this.refreshIntervalId);
  },
  methods: {
    clickOnFile(file) {
      this.$store.commit("setShowLog", false);
      const fileType = common.getFileType(file.filePath);
      if (fileType === "lua" || fileType === "txt" || fileType === "img") {
        this.getFileContent(file.filePath);
      }
    },
    hasFiles() {
      return this.files && this.files.length > 0;
    },
    loadFiles() {
      let self = this;

      //   var files = require("../test-data.json");
      //   self.files = files.files;
      //   return;

      var filePath = this.currentPath;
      var url = "/files?path=" + encodeURIComponent(filePath);
      axios
        .get(url)
        .then(function(response) {
          self.files = response.data.files;
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        });
    },
    getRunningScripts() {
      let self = this;

      var url = "/runningScripts";
      axios
        .get(url)
        .then(function(response) {
          self.runningScripts = response.data;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    },
    fileName(file) {
      var fileName = common.getFilename(file.filePath);
      return fileName;
    },
    fileIcon(iconName) {
      return "/icons/" + iconName;
    },
    fileNameLink(file) {
      var filePath = file.filePath;
      var iconName = file.iconName;

      var link;
      if (common.isFolder(iconName)) {
        link = "/?path=" + encodeURIComponent(filePath);
      } else if (common.isImage(iconName)) {
        link = "/file/view?path=" + encodeURIComponent(filePath);
      } else {
        link = "/file/edit?path=" + encodeURIComponent(filePath);
      }
      return link;
    },
    play(filePath) {
      const self = this;
      var url = "/control/start_playing?path=" + encodeURIComponent(filePath);
      axios
        .get(url)
        .then(function(response) {
          var respObj = response.data;
          if (respObj.status != "success") {
            self.$notify({
              group: "main",
              type: "error",
              text: respObj.info
            });
          }
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        });
    },
    stop(filePath) {
      const self = this;
      var url = "/control/stop_playing?path=" + encodeURIComponent(filePath);
      axios
        .get(url)
        .then(function(response) {
          var respObj = response.data;
          if (respObj.status != "success") {
            self.$notify({
              group: "main",
              type: "error",
              text: respObj.info
            });
          }
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        });
    },
    renameFile(oldFilePath) {
      let self = this;

      var newFilename = prompt("Please input the new filename", "");
      if (newFilename) {
        var newFilePath = self.currentPath + "/" + newFilename;
        askServerToRenameFile(oldFilePath, newFilePath);
      }

      function askServerToRenameFile(oldFilePath, newFilePath) {
        var url =
          "/file/rename?path=" +
          encodeURIComponent(oldFilePath) +
          "&newPath=" +
          encodeURIComponent(newFilePath);

        axios
          .get(url)
          .then(function(response) {
            var respObj = response.data;
            if (respObj.status == "success") {
              self.loadFiles();
            } else {
              self.$notify({
                group: "main",
                type: "error",
                text: respObj.info
              });
            }
          })
          .catch(function(error) {
            self.$notify({ group: "main", type: "error", text: error });
          });
      }
    },
    deleteFile(filePath) {
      let self = this;
      if (confirm("Are you sure to delete the file?")) {
        var url = "/file/delete?path=" + encodeURIComponent(filePath);
        axios
          .get(url)
          .then(function(response) {
            var respObj = response.data;
            if (respObj.status == "success") {
              self.loadFiles();
            } else {
              self.$notify({
                group: "main",
                type: "error",
                text: respObj.info
              });
            }
          })
          .catch(function(error) {
            self.$notify({ group: "main", type: "error", text: error });
          });
      }
    },
    isFolder(iconName) {
      return common.isFolder(iconName);
    },
    isLuaPackage(filePath) {
      return common.isLuaPackage(filePath);
    },
    isScript(filePath) {
      return common.isScript(filePath);
    },
    isRunning(filePath) {
      return this.runningScripts.includes(filePath);
    },
    newFile() {
      let self = this;

      var filename = prompt("Please input the filename", "");
      if (filename) {
        if (
          !filename.toLowerCase().endsWith(".txt") &&
          !filename.toLowerCase().endsWith(".lua")
        ) {
          filename = filename + ".lua";
        }
        var newFilePath = self.currentPath + "/" + filename;
        askServerToNewFile(newFilePath);
      }

      function askServerToNewFile(filePath) {
        var url = "/file/new?path=" + encodeURIComponent(filePath);
        axios
          .get(url)
          .then(function(response) {
            var respObj = response.data;
            if (respObj.status == "success") {
              self.loadFiles();
            } else {
              self.$notify({
                group: "main",
                type: "error",
                text: respObj.info
              });
            }
          })
          .catch(function(error) {
            self.$notify({ group: "main", type: "error", text: error });
          });
      }
    },
    newFolder() {
      let self = this;

      var filename = prompt("Please input the folder name", "");
      if (filename) {
        var newFilePath = self.currentPath + "/" + filename;
        askServerToNewFile(newFilePath);
      }

      function askServerToNewFile(filePath) {
        var url = "/file/newFolder?path=" + encodeURIComponent(filePath);
        axios
          .get(url)
          .then(function(response) {
            var respObj = response.data;
            if (respObj.status == "success") {
              self.loadFiles();
            } else {
              self.$notify({
                group: "main",
                type: "error",
                text: respObj.info
              });
            }
          })
          .catch(function(error) {
            self.$notify({ group: "main", type: "error", text: error });
          });
      }
    },
    uploadFile() {
      const self = this;
      var file = document.getElementById("file").files[0];
      if (file) {
        var filePath = this.currentPath + "/" + file.name;
        var action = "/file/upload?path=" + encodeURIComponent(filePath);

        var formData = new FormData();
        formData.append("file", file);
        axios
          .post(action, formData, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(function(response) {
            var respObj = response.data;
            if (respObj.status == "success") {
              self.loadFiles();
              self.$refs["modal-upload-file"].hide();
              self.$notify({
                group: "main",
                type: "success",
                text: "Uploaded successfully!"
              });
            } else {
              self.$notify({
                group: "main",
                type: "error",
                text: respObj.info
              });
            }
          })
          .catch(function(error) {
            self.$notify({ group: "main", type: "error", text: error });
          });
      }
    },
    getFileContent(filePath) {
      let self = this;

      self.$store.commit("setShowLoading", true);

      var url = "/file/content?path=" + encodeURIComponent(filePath);
      axios
        .get(url)
        .then(function(response) {
          var respObj = response.data;
          if (respObj.status === "success") {
            const content = respObj.content;
            self.$store.commit("setCurrentFile", {
              filePath,
              content
            });
          } else {
            self.$notify({
              group: "main",
              type: "error",
              text: respObj.info
            });
          }
        })
        .catch(function(error) {
          self.$notify({ group: "main", type: "error", text: error });
        })
        .finally(function() {
          self.$store.commit("setShowLoading", false);
        });
    }
  }
};
</script>
