  .log {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    min-width: 500px;

    border: 1px solid #eeeeee;
    padding: 10px;

    pre {
      width: 100%;
      height: 100%;
    }
  }
}
</style>

